<template>
  <div class="start">
    <h1 class="start__title">
      あなただけのボディーガード<img src="../../public/img/logo.svg" alt="" />
    </h1>
    <p class="start__text">
      <a href="#">利用規約</a>と<a href="#">個人情報の取り扱い</a>に同意して
    </p>
    <button class="btn" @click="toSignup">新規登録</button>
    <button class="btn loginBtn" @click="toSignin">ログイン</button>
  </div>
</template>

<script>
export default {
  methods: {
    toSignup() {
      this.$router.push({ path: "/tutorial" });
    },
    toSignin() {
      this.$router.push({ path: "/signin" });
    },
  },
  beforeCreate() {
    if (localStorage.getItem("userName")) {
      this.$router.push({ path: "/" });
    } else if (localStorage.getItem("uid")) {
      this.$router.push({ path: "/createAccount" });
    }
  },
};
</script>

<style lang="scss" scoped>
@import "../css/start.css";
</style>
