<template>
  <div class="form">
    <h1 class="sign__ttl">メール認証</h1>
    <div v-if="!err">
      <div class="sign__ttl--2">メールアドレスが確認されました。</div>
      <div class="sign__txt--2">
        ご入力いただいたメールアドレスが確認されましたので、続けてアカウント登録へにお進みください。
      </div>
      <button class="sign__form--btn true" @click="createAccount">
        アカウント登録へ
      </button>
    </div>

    <div v-if="err">
      <div class="sign__ttl--2">メールアドレスが確認できませんでした。</div>
      <div class="sign__txt--2">
        メールアドレスの確認に失敗したため、もう一度登録してください。
      </div>
      <button class="sign__form--btn true" @click="toSignup">新規登録へ</button>
    </div>
  </div>
</template>

<script>
import {
  getAuth,
  isSignInWithEmailLink,
  signInWithEmailLink,
} from "firebase/auth";
export default {
  data() {
    return {
      email: null,
      err: false,
    };
  },
  methods: {
    createAccount() {
      this.$router.push({ path: "/createaccount" });
    },
    toSignup() {
      this.$router.push({ path: "/" });
    },
  },
  created() {
    const auth = getAuth();
    if (isSignInWithEmailLink(auth, window.location.href)) {
      let email = window.localStorage.getItem("emailForSignIn");
      if (!email) {
        email = window.prompt("確認のためメールアドレスを再入力してください。");
        window.localStorage.setItem("emailForSignIn", email);
      }
      signInWithEmailLink(auth, email, window.location.href)
        .then((result) => {
          localStorage.setItem("uid", result.user.uid);
        })
        .catch(() => {
          this.err = true;
        });
    } else {
      this.err = true;
    }
  },
};
</script>

<style lang="scss" scoped>
@import "../css/sign.css";
</style>
