<template>
  <div class="mypage footer-pb bgc">
    <div v-if="!edit">
      <app-header :header="{ notification: true, title: 'マイページ' }" />
      <div class="account__profile">
        <div>
          <div class="account__profile--icon">
            <img
              :src="userData.userImg"
              :alt="userData.username + 'のプロフィール画像'"
              v-if="userData.userImg"
            />
            <svg
              v-if="!userData.userImg"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 394.56 394.53"
            >
              <defs></defs>
              <path
                d="M198.07,0C87.76-.25.68,88.79,0,196c-.69,109.31,88,198.6,196.71,198.5,107.76.66,197.24-86.39,197.85-196.13C395.17,89.34,306.86.25,198.07,0Zm-.23,59.33c32.28-.34,59,26.88,58.85,59.21a59.21,59.21,0,1,1-118.42-.4C138.62,83.74,166.81,58.68,197.84,59.33Zm116.43,219a142.78,142.78,0,0,1-104.81,60.56,94.58,94.58,0,0,1-12.72.62c-36.44-.79-68.42-12.95-95.6-37.46a154.84,154.84,0,0,1-20.62-23.31A8.64,8.64,0,0,1,79,271.49c1.76-9.37,7-16.62,14-22.73,13.44-11.75,29.5-18.5,46.23-23.87a215.44,215.44,0,0,1,43.9-9.1c14.93-1.52,29.72-.33,44.41,2.49,19.72,3.78,38.77,9.53,56.49,19.15,10,5.45,19.14,12.07,25.71,21.66a33.21,33.21,0,0,1,5.58,14.8A5.93,5.93,0,0,1,314.27,278.35Z"
              />
            </svg>
            <div v-if="!userData.userImg" class="account__icon--edit"></div>
            <div class="account__icon--balloon">
              {{ userData.statusMessage.slice(0, 30).slice(0, 30) }}
            </div>
          </div>
          <button @click="profileEdit" class="account__profile--edit">
            プロフィールを変更する
          </button>
        </div>
      </div>
      <div class="account__info">
        <div class="account__info--name">{{ userData.username }}</div>
        <div class="account__info--txt">
          <p>{{ userData.email }}</p>
          <p>
            お誕生日：{{ userData.birthday.slice(-5, -3) }}月{{
              userData.birthday.slice(-2)
            }}日
          </p>
        </div>
        <section>
          <h2>予約状況</h2>
          <div class="account__info--date">
            <p>9<span>月</span>3<span>日</span>（金）</p>
            <p>
              21<span>時</span>30<span>分</span
              ><span class="tilde">〜</span>22<span>時</span>30<span>分</span>
            </p>
          </div>
        </section>
        <a href="#" class="account__info--setting">メールアドレスを変更する</a>
        <a href="#" class="account__info--setting">パスワードを変更する</a>
        <a href="#" @click.prevent="logout" class="account__info--setting red"
          >ログアウト</a
        >
      </div>
    </div>

    <div v-if="edit">
      <app-header
        :header="{
          notification: true,
          reloadBtn: true,
          title: 'プロフィールを変更',
        }"
        @my-click="edit = false"
      />
      <div class="account__profile">
        <div>
          <div class="account__profile--icon">
            <img
              :src="userData.userImg"
              :alt="userData.username + 'のプロフィール画像'"
              v-if="userData.userImg"
            />
            <svg
              v-if="!userData.userImg"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 394.56 394.53"
            >
              <defs></defs>
              <path
                d="M198.07,0C87.76-.25.68,88.79,0,196c-.69,109.31,88,198.6,196.71,198.5,107.76.66,197.24-86.39,197.85-196.13C395.17,89.34,306.86.25,198.07,0Zm-.23,59.33c32.28-.34,59,26.88,58.85,59.21a59.21,59.21,0,1,1-118.42-.4C138.62,83.74,166.81,58.68,197.84,59.33Zm116.43,219a142.78,142.78,0,0,1-104.81,60.56,94.58,94.58,0,0,1-12.72.62c-36.44-.79-68.42-12.95-95.6-37.46a154.84,154.84,0,0,1-20.62-23.31A8.64,8.64,0,0,1,79,271.49c1.76-9.37,7-16.62,14-22.73,13.44-11.75,29.5-18.5,46.23-23.87a215.44,215.44,0,0,1,43.9-9.1c14.93-1.52,29.72-.33,44.41,2.49,19.72,3.78,38.77,9.53,56.49,19.15,10,5.45,19.14,12.07,25.71,21.66a33.21,33.21,0,0,1,5.58,14.8A5.93,5.93,0,0,1,314.27,278.35Z"
              />
            </svg>
            <div v-if="!userData.userImg" class="account__icon--edit"></div>
            <div class="account__icon--balloon">
              {{ userData.statusMessage.slice(0, 30) }}
            </div>
          </div>
          <label for="inputImg">
            <p class="account__profile--edit">画像を変更する</p>
          </label>
          <input
            id="inputImg"
            class="account__profile--input"
            type="file"
            accept="image/*"
            @change="inputUserImg"
          />
        </div>
      </div>
      <div class="account__edit">
        <div class="account__edit--ttl">名前</div>
        <input
          type="text"
          class="account__edit--value"
          :placeholder="userData.username"
          v-model="inputData.username"
        />
        <div class="account__edit--ttl">生年月日</div>
        <input
          type="date"
          v-model="inputBirthday"
          class="account__edit--value"
        />
        <div class="account__edit--ttl">性別</div>
        <select
          name="gender"
          v-model="inputGender"
          class="account__edit--value"
        >
          <option value="female">女性</option>
          <option value="male">男性</option>
          <option value="other">無回答</option>
        </select>
        <div class="account__edit--ttl">
          一言コメント<span class="account__edit--message"
            >最大30文字まで入力できます。</span
          >
        </div>
        <input
          type="text"
          class="account__edit--value"
          :placeholder="userData.statusMessage"
          v-model="inputData.statusMessage"
        />
        <button @click="editEnd">完了する</button>
      </div>
    </div>
    <app-footer selected="4" />
  </div>
</template>

<script>
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";
import { getDatabase, update, ref, child, get } from "firebase/database";
import AppHeader from "../components/AppHeader.vue";
import AppFooter from "../components/AppFooter.vue";

export default {
  data() {
    return {
      edit: false,
      inputData: {
        username: null,
        birthday: null,
        gender: null,
        statusMessage: null,
        userImg: null,
      },
      userData: {
        username: "test user",
        birthday: "2002-02-06",
        email: "okazaki200226@gmail.com",
        gender: "male",
        statusMessage: "hi im test user!!",
        userImg: null,
      },
    };
  },
  components: {
    AppHeader,
    AppFooter,
  },
  computed: {
    inputBirthday: {
      get() {
        return this.userData.birthday;
      },
      set(value) {
        this.inputData.birthday = value;
      },
    },
    inputGender: {
      get() {
        return this.userData.gender;
      },
      set(value) {
        this.inputData.gender = value;
      },
    },
  },
  methods: {
    profileEdit() {
      this.edit = true;
    },
    inputUserImg(e) {
      const image = e.target.files[0];
      this.createImage(image);
    },
    createImage(image) {
      const reader = new FileReader();
      // imageをreaderにDataURLとしてattachする
      reader.readAsDataURL(image);
      // readAdDataURLが完了したあと実行される処理
      reader.onload = () => {
        this.userData.userImg = reader.result;
        this.inputData.userImg = reader.result;
      };
    },
    editEnd() {
      const db = getDatabase();
      const updates = {};

      if (this.inputData.username) {
        updates["/users/" + localStorage.getItem("uid") + "/username"] =
          this.inputData.username;
        this.userData.username = this.inputData.username;
        this.inputData.username = null;
      }
      if (this.inputData.birthday) {
        updates["/users/" + localStorage.getItem("uid") + "/birthday"] =
          this.inputData.birthday;
        this.userData.birthday = this.inputData.birthday;
        this.inputData.birthday = null;
      }
      if (this.inputData.gender) {
        updates["/users/" + localStorage.getItem("uid") + "/gender"] =
          this.inputData.gender;
        this.inputData.gender = null;
      }
      if (this.inputData.statusMessage) {
        updates["/users/" + localStorage.getItem("uid") + "/statusMessage"] =
          this.inputData.statusMessage;
        this.userData.statusMessage = this.inputData.statusMessage;
        this.inputData.statusMessage = null;
      }
      if (this.inputData.userImg) {
        updates["/users/" + localStorage.getItem("uid") + "/userImg"] =
          this.inputData.userImg;
        this.userData.userImg = this.inputData.userImg;
        this.inputData.userImg = null;
      }

      update(ref(db), updates);
      const dbRef = ref(getDatabase());
      get(child(dbRef, `/users/${localStorage.getItem("uid")}`))
        .then((snapshot) => {
          if (snapshot.exists()) {
            this.userData = snapshot.val();
          } else {
            this.$router.push({ path: "/createAccount" });
          }
        })
        .catch(() => {
          this.$router.push({ path: "/signin" });
        });

      // プロフィールの編集が終了したらfalseにする
      this.edit = false;
    },
    logout() {
      const auth = getAuth();
      signOut(auth)
        .then(() => {
          this.$router.push({ path: "/start" });
        })
        .catch((error) => {
          console.log(error);
        });
      localStorage.clear();
    },
  },
  created() {
    // もしlocalStorageに情報があればuserdataに代入する
    if (localStorage.getItem("username"))
      this.userData.username = localStorage.getItem("username");
    if (localStorage.getItem("statusMessage"))
      this.userData.statusMessage = localStorage.getItem("statusMessage");
    if (localStorage.getItem("userImg"))
      this.userData.userImg = localStorage.getItem("userImg");

    // データベースに接続する
    const dbRef = ref(getDatabase());
    get(child(dbRef, `/users/${localStorage.getItem("uid")}`))
      .then((snapshot) => {
        if (snapshot.exists()) {
          this.userData = snapshot.val();

          localStorage.setItem("username", snapshot.val().username);
          localStorage.setItem("statusMessage", snapshot.val().statusMessage);
          localStorage.setItem("userImg", snapshot.val().userImg);
        } else {
          this.$router.push({ path: "/createAccount" });
        }
      })
      .catch(() => {
        this.$router.push({ path: "/signin" });
      });
  },
  beforeCreate() {
    // auth で uid と username の再取得する
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        localStorage.setItem("uid", user.uid);
      } else {
        this.$router.push({ path: "/signin" });
      }
    });
  },
};
</script>

<style lang="scss" scoped>
@import "../css/common.css";
@import "../css/account.css";
</style>
