      <div class="home__banners f-bet">
        <button class="home__banner-small">
          <img src="../../public/img/home__banner1-small.png" />
        </button>
        <button class="home__banner-small">
          <img src="../../public/img/home__banner2-small.png" />
        </button>
      </div>

      <div class="home__columns">
        <h3 class="home__subTitle">コラム</h3>
        <ul>
          <li class="f-bet">
            <img src="../../public/img/home__column-img1.jpg" />
            <div class="f-bet">
              <h4>ボクシング世界チャンピオンから<br />見るFriboの安全性</h4>
