<template>
  <div>
    <h1 class="sign__ttl">アカウント作成</h1>

    <!-- メールで新規登録する方 -->
    <div v-if="!googleSignIn">
      <!-- 1page -->
      <div class="form" v-if="!next">
        <div class="sign__form--ttl" id="ttl1">
          <p>ユーザーネーム</p>
          <span v-text="errMessage[0]"></span>
        </div>
        <input
          v-model="username"
          @change="change1"
          type="text"
          placeholder="10文字以内で入力してください"
          class="sign__form--value"
        />
        <div class="sign__form--ttl" id="ttl2">
          <p>パスワード</p>
          <span v-text="errMessage[1]"></span>
        </div>
        <input
          v-model="pass1"
          @change="change2"
          type="password"
          placeholder="半角英数字で8文字以上"
          class="sign__form--value"
        />
        <div class="sign__form--ttl" id="ttl3">
          <p>パスワード（再入力）</p>
          <span v-text="errMessage[2]"></span>
        </div>
        <input
          v-model="pass2"
          @change="change3"
          type="password"
          placeholder="半角英数字で8文字以上"
          class="sign__form--value"
        />
        <button
          @click="nextPage"
          :class="{
            'sign__form--btn': true,
            true: flag[0] && flag[1] && flag[2],
          }"
        >
          次へ
        </button>
      </div>

      <!-- 2page -->
      <div class="form" v-if="next">
        <div class="sign__form--ttl">性別</div>
        <select @change="change4" v-model="gender" class="sign__form--value">
          <option value="" selected>-</option>
          <option value="female">女性</option>
          <option value="male">男性</option>
          <option value="other">その他</option>
        </select>
        <div class="sign__form--ttl">生年月日</div>
        <input
          @change="change5"
          type="date"
          class="sign__form--value"
          v-model="birthday"
        />

        <button
          @click="createAccount"
          :class="{ 'sign__form--btn': true, true: flag[3] && flag[4] }"
        >
          登録する
        </button>
      </div>
    </div>

    <!-- Googleアカウントで登録する方 -->
    <div v-if="googleSignIn">
      <!-- 1page -->
      <div class="form" v-if="!next">
        <div class="sign__form--ttl" id="ttl1">
          <p>ユーザーネーム</p>
          <span v-text="errMessage[0]"></span>
        </div>
        <input
          v-model="username"
          @change="change1"
          type="text"
          placeholder="10文字以内で入力してください"
          class="sign__form--value"
        />
        <div class="sign__form--ttl">性別</div>
        <select @change="change4" v-model="gender" class="sign__form--value">
          <option value="" selected>-</option>
          <option value="female">女性</option>
          <option value="male">男性</option>
          <option value="other">無回答</option>
        </select>
        <div class="sign__form--ttl">生年月日</div>
        <input
          @change="change5"
          type="date"
          class="sign__form--value"
          v-model="birthday"
        />

        <button
          @click="createAccount"
          :class="{
            'sign__form--btn': true,
            true: flag[0] && flag[3] && flag[4],
          }"
        >
          登録する
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { getAuth, updatePassword, updateProfile } from "firebase/auth";
import { getDatabase, ref, set, child, get } from "firebase/database";

export default {
  data() {
    return {
      text1: "※10文字以内で入力してください",
      text2: "※半角英数字で8文字以上で入力してください",
      text3: "※入力されたパスワードが一致しません",
      text4: "※ユーザーネームを入力してください",
      username: null,
      pass1: null,
      pass2: null,
      gender: "",
      birthday: "",
      flag: [false, false, false, false, false],
      errMessage: [null, null, null],
      next: false,
      googleSignIn: false,
    };
  },
  methods: {
    change1() {
      // username
      if (this.username === "") {
        this.errMessage[0] = this.text4;
        this.flag[0] = false;
        return;
      } else if (this.username.length >= 10) {
        this.errMessage[0] = this.text1;
        this.flag[0] = false;
        return;
      }
      this.flag[0] = true;
      this.errMessage[0] = "";
    },
    change2() {
      // pass1
      if (!this.pass1.match(/^[A-Za-z0-9]+$/) || this.pass1.length < 8) {
        this.errMessage[1] = this.text2;
        this.flag[1] = false;
        return;
      }
      this.flag[1] = true;
      this.errMessage[1] = "";
      this.flag[2] = false;
      this.pass2 = null;
      this.errMessage[2] = "";
    },
    change3() {
      // pass2
      if (this.pass1 !== this.pass2) {
        this.errMessage[2] = this.text3;
        this.flag[2] = false;
        return;
      }
      this.flag[2] = true;
      this.errMessage[2] = "";
    },
    change4() {
      if (this.gender == "") {
        this.flag[3] = false;
        return;
      }
      this.flag[3] = true;
    },
    change5() {
      if (this.birthday == "") {
        this.flag[4] = false;
        return;
      }
      this.flag[4] = true;
    },
    nextPage() {
      if (this.flag[0] && this.flag[1] && this.flag[2]) {
        this.next = true;
      }
    },
    createAccount() {
      // パスワードの設定
      if (
        this.flag[0] &&
        this.flag[1] &&
        this.flag[2] &&
        this.flag[3] &&
        this.flag[4]
      ) {
        const auth = getAuth();
        const user = auth.currentUser;
        const newPassword = this.pass2;
        updatePassword(user, newPassword)
          .then(() => {
            console.log("パスワード設定完了");
            updateProfile(auth.currentUser, {
              displayName: this.username,
            })
              .then(() => {
                const db = getDatabase();
                set(ref(db, "users/" + auth.currentUser.uid), {
                  username: this.username,
                  email: auth.currentUser.email,
                  userImg: auth.currentUser.photoURL,
                  gender: this.gender,
                  birthday: this.birthday,
                  statusMessage: "よろしくお願いします！",
                });

                localStorage.setItem("userName", this.username);
                this.$router.push({ path: "/" });
                return;
              })
              .catch((error) => {
                console.log(error);
              });
          })
          .catch((error) => {
            console.log(error);
          });
      }

      // ユーザーネームの設定
      if (this.flag[0]) {
        const auth = getAuth();
        updateProfile(auth.currentUser, {
          displayName: this.username,
        })
          .then(() => {
            const db = getDatabase();
            set(ref(db, "users/" + auth.currentUser.uid), {
              username: this.username,
              email: auth.currentUser.email,
              userImg: auth.currentUser.photoURL,
              gender: this.gender,
              birthday: this.birthday,
              statusMessage: "よろしくお願いします！",
            });

            localStorage.setItem("userName", this.username);
            this.$router.push({ path: "/" });
            return;
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
  created() {
    // Googleでサインアップしようとしたときtrueへ
    if (!localStorage.getItem("emailForSignIn")) {
      this.googleSignIn = true;
    }
  },
  beforeCreate() {
    const auth = getAuth();
    if (auth.currentUser !== null) {
      console.dir(auth.currentUser);
      localStorage.setItem("uid", auth.currentUser.uid);
    } else {
      this.$router.push({ path: "/signin" });
      return;
    }

    const dbRef = ref(getDatabase());
    get(child(dbRef, `users/${localStorage.getItem("uid")}`))
      .then((snapshot) => {
        if (snapshot.exists()) {
          this.$router.push({ path: "/" });
        } else {
          this.$router.push({ path: "/createAccount" });
        }
      })
      .catch(() => {
        this.$router.push({ path: "/signin" });
      });
  },
};
</script>

<style lang="scss" scoped>
@import "../css/sign.css";
</style>
