<template>
  <header class="header">
    <button
      :class="{
        header__button: true,
        'header__backButton--true': header.backBtn || header.reloadBtn,
      }"
      @click="routerback"
    ></button>

    <h1 class="header__title">{{ header.title }}</h1>

    <button
      :class="{
        header__button: true,
        'header__notificationButton--true': header.notification,
      }"
      @click="toNotification"
    >
      {{ header.text }}
    </button>
  </header>
</template>

<script>
export default {
  props: {
    header: {
      type: Object,
      default() {
        return {
          backBtn: false,
          title: "NO TITLE",
          notification: false,
          reloadBtn: false,
          text: null,
        };
      },
    },
  },
  methods: {
    routerback() {
      if (this.header.backBtn) this.$router.go(-1);
      if (this.header.reloadBtn) this.$emit("my-click");
    },
    toNotification() {
      this.$router.push({ path: "/notification" });
    },
  },
};
</script>

