.header {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 25px;
  padding-top: 7px;
  border-bottom: 1px solid #cacaca;
  background-color: white;

  &__button {
    min-width: 20px;
    min-height: 20px;
  }

  &__title {
    font-style: normal;
    font-size: 16px;
    line-height: 150%;
  }

  &__backButton--true {
    background-image: url("../../public/img/header__backBtn.svg");
  }
  &__notificationButton--true {
    background-image: url("../../public/img/header__notification.svg");
  }
}
</style>
