<template>
  <div class="tutorial">
    <div class="wrap tutorial_2" v-if="page == 0">
      <h1 class="title tutorial_2_Tl">
        <img src="../../public/img/logo.svg" alt="ロゴアイコンです" /><span
          >って？</span
        >
      </h1>
      <p class="tutorial_2_Txt">
        夜道が怖いときにプロの格闘家が<br />
        あなたのボディーガードになり、<br />
        あなたを守るサービスです。
      </p>
      <img
        class="tutorial__description--img"
        src="../../public/img/description.jpg"
        alt="人を待っている画像"
      />
    </div>

    <div class="wrap" v-if="page != 0">
      <h1 class="step_Tl">STEP{{ page }}</h1>
      <p class="step_Txt">{{ texts[page - 1] }}</p>
      <img class="step_Img" :src="`./img/tutorial_0${page}.jpg`" />
      <div class="circle_g">
        <div :class="{ circle: true, circle_step: page == 1 }"></div>
        <div :class="{ circle: true, circle_step: page == 2 }"></div>
        <div :class="{ circle: true, circle_step: page == 3 }"></div>
        <div :class="{ circle: true, circle_step: page == 4 }"></div>
      </div>
    </div>
    <button @click="nextPage" class="btn nextBtn" v-if="page != 4">
      つぎへ
    </button>
    <button @click="toSignup" class="btn nextBtn" v-if="page == 4">
      新規登録
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      page: 0,
      texts: [
        "まずは新規登録しよう！",
        "ボディーガードをリクエストする！",
        "待ち合わせして出発！",
        "安全に家まで帰ろう！",
      ],
    };
  },
  methods: {
    nextPage() {
      this.page++;
    },
    toSignup() {
      this.$router.push({ path: "/signup" });
    },
  },
  beforeCreate() {
    if (localStorage.getItem("userName")) {
      this.$router.push({ path: "/" });
    } else if (localStorage.getItem("uid")) {
      this.$router.push({ path: "/createAccount" });
    }
  },
};
</script>

<style lang="scss" scoped>
@import "../css/start.css";
</style>
